export default {
  data() {
    return {
      cartInfo: [
          {
              id:0,
              img:'',
              name:'',
              skuInfo:{
                  name:''
              },
            
          }
      ]
    }
  },
  computed: {
      status(){
          if(this.cartInfo.length > 0 ){
            return true
          }else {
              return false
          }
      }
  },
  created() {},
  mounted() {
      
  }
}



